<template>
 <div id="view-employee">
  <ul class="collection with-header">
   <li class="collection-header"><h4>{{employee.name}}</h4></li>
   <li class="collection-item">Employee ID: {{employee.id}}</li>
   <li class="collection-item">Email: {{employee.email}}</li>
   <li class="collection-item">Position: {{employee.position}}</li>
   <li class="collection-item">Department: {{employee.department}}</li>
  </ul>
  <router-link to="/" class="btn grey">
   Back
  </router-link>
  <button  class="btn red" @click.prevent="deleteEmployee">
   Delete
  </button>
  <div class="fixed-action-btn">
    <router-link :to="{name:'edit-employee', params:{employee_id:employee.id}}" class="btn-floating btn-large red">
      <i class="fa fa-pencil"></i>
    </router-link>
    </div>
 </div>
</template>
<script>
import db from "../firebase_services/firebaseinit";
export default {
 name:'view-employee',
 data(){
  return{
   employee:{}
  }
 },
 beforeRouteEnter (to,from,next) {
  db.collection('employees').where('id','==',to.params.employee_id).get()
  .then(querySnapshot=>{
   querySnapshot.forEach(doc=>{
    next(vm=>{
     vm.employee=doc.data()
    })
   })
  })
 },
 watch:{
  '$route':'fetchData'
 },
 methods:{
  fetchData(){
   db.collection('employees').where('id','==', this.$route.params.employee_id).get()
   .then(querySnapshot=>{
    querySnapshot.forEach(doc=>{
     this.employee = doc.data()
    })
   })
  },
  deleteEmployee(){
   if(confirm('are you sure?')){
db.collection('employees').where('id','==', this.$route.params.employee_id).get()
   .then(querySnapshot=>{
    querySnapshot.forEach(doc=>{
     doc.ref.delete()
     this.$router.push('/')
    })
   })
   }
  }
 }
}
</script>